import React, {useState} from 'react';
import { TasksList } from '../../store/reducer';
import  TaskItem from '../TaskItem/TaskItem';
import ModalForm, {Values} from '../ModalForm/ModalForm';

interface Props {
  tasksList: Array<TasksList>;
  categoryTitle: string;
}

function TasksTable(props: Props) {
  const { tasksList, categoryTitle } = props;
  const [isVisible, setVisibility] = useState(false);
  const addTask = () => {
  		setVisibility(true);
  }
  const okClick = (values: Array<Values>) => {
  	if(values[0].value.trim().length > 0)
  	setVisibility(false);
  }
  const cancelClick = () =>{
  	setVisibility(false);
  }

  return (
  	<>
	  	<div className="task-title">{categoryTitle}</div>
	    <div className="task-list">
	      {
	      	tasksList.map((itemData, idx) => (
	      		<TaskItem 
	      			key={itemData.id + 'task-item'}
	      			itemData={itemData}/>))
	      }
	      <div className="task-add-button" onClick={addTask}>+</div>
	      <ModalForm 
	      	inputType=""
	      	title="Create new task" 
	        visible={isVisible} 
	        label={['Input task description']}
	        inputName={['task description']}
	        onOK={okClick}
	        onCancel={cancelClick} 
	        placeHolder={['Some description...']}
	      />
	    </div>
    </>
  );
}

export default TasksTable;
