import React from 'react';
import { useState } from 'react';
import './modal.scss';

interface Props{
	title:string;
	visible:boolean;
	inputName: Array<string>;
	placeHolder: Array<string>;
	label: Array<string>;
	onOK: (values: Array<ValueObject>) => void;
	onCancel: () => void;
	inputType: string;
}


class ValueObject{
	public value: string;
	public name: string;
	constructor(){
		this.value = '';
		this.name = '';
	}
}

export interface Values extends ValueObject{}


function  ModalForm<FC>(props: Props){
	const { 
		title, 
		visible, 
		inputName, 
		placeHolder, 
		label,
		onOK,
		onCancel,
		inputType,
	} = props;
	const valuesObject:Array<ValueObject> = inputName.map(i => new ValueObject)
	const [value, setValue] = useState(valuesObject);

	const inputChange = (e:React.ChangeEvent, idx:number) => {
		const item = e.target as HTMLInputElement;
		const inputValue = item.value;
		const newValue = [...value];
		newValue[idx].value = inputValue;
		if(inputValue.length < 20 && inputType === 'input')
			setValue(newValue);
	}

	const onFocus:(e:React.FocusEvent) => void = (e:React.FocusEvent) => {
		//
	} 

	const wrapper = inputName.map((i, idx) =>(
		<div className="modal-input">
		<label>{label[idx]}
			{inputType ==='input'? <input 
				name={i} 
				onChange={(e) => inputChange(e, idx)}
				value={value[idx].value}
				onFocus={(e) => onFocus(e)}
				placeholder={placeHolder[idx]}/>
				: <textarea 
						onChange={(e) => inputChange(e, idx)}
						onFocus={(e) => onFocus(e)}
						placeholder={placeHolder[idx]}
						value={value[idx].value}
						rows={5} 
						cols={45}>
				</textarea>
			}	
			</label>
		</div>
	));
	
	return (
			<>{visible &&
					<div className="modal-background"></div>
				}
				{visible &&
					<section className="modal-input-group">
						<h3>{title}</h3>
						{wrapper}
						<div className="modal-input-footer">
							<button 
								role="button" 
								type="button" 
								onClick={onCancel}
								name="cancel">Cancel</button>
							<button 
								role="button" 
								type="button" 
								onClick={() => onOK(value)}
								name="submit">OK</button>
						</div>
					</section>
				}
			</>
		)
}

export default ModalForm;
