import React from 'react';
import './index.scss';
import { connect } from 'react-redux';
import { StateInit } from '../store/reducer';
import TodoCategories from './TodosCategory/TodoCategory';
import TasksTable from './TasksTable/TasksTable';
import ModalForm, { Values } from './ModalForm/ModalForm';

interface Props {
  state: StateInit;
  addTask: (values: Array<Values>) => void;
  removeModal: () => void;
}

interface Dispatch {
  (type: { type: string, payload?: Array<Values> }): void;
}

function Todos(props: Props) {
  const { addTask, state, removeModal } = props;
  const { isVisibleModal } = state;
  const currentCategoryElement = state.categoryes.filter(
    (item) => item.id === state.currentCategoryId
  )[0];
  const categoryTitle: string = currentCategoryElement.description;
  const okClick = (values: Array<Values>) => {
    if(values[0].value.trim().length > 0){
      addTask(values);
      removeModal();
    }
    
  };
  const cancelClick = () => {
    removeModal();
  };
  console.log(state);
  return (
    <>
      <ModalForm 
        inputType="input"
        title="Create new category" 
        visible={isVisibleModal} 
        label={['Input category name']}
        inputName={['categoryName']}
        onOK={okClick}
        onCancel={cancelClick} 
        placeHolder={['Homework']}/>
      <div className="container">
        <TodoCategories />
        <TasksTable 
          categoryTitle={categoryTitle}
          tasksList={currentCategoryElement.tasksList} />
      </div>
    </>
  );
}

const mapStetaToProps = (state: StateInit) => ({ state });
const mapDispatchToProps = (dispatch: Dispatch) => {
  return { 
    addTask: (values: Array<Values>) => dispatch({ type: 'ADD_CATEGORY', payload: values}),
    removeModal: () => dispatch({ type: 'REMOVE_MODAL' })
  };
};

const connector = connect(mapStetaToProps, mapDispatchToProps);

export default connector(Todos);
