import React from 'react';
import { Draggable } from 'react-beautiful-dnd'

interface CItemProps{
	idx: number;
	keyDown: () => void;
	itemClick: (itemId: number) => void;
	itemDescription: string;
	itemId: number; 
	curCatId: number;
	innerRef?: any;
}

const CategoryItem:React.FC<CItemProps> = (props) => {
	const {
		idx, 
		keyDown,
		itemClick,
		itemDescription, 
		itemId, 
		curCatId} = props;

	return (
		<Draggable index={idx}  draggableId={`item-${itemId}`}>
	    {(provided, snapshot) => (
					<div
						ref={provided.innerRef}
			      role="button"
			      tabIndex={idx}
			      onKeyDown={keyDown}
			      className={`cg-item ${(curCatId === itemId)? 'active' : ''}`}
			      onClick={() => itemClick(itemId)}
			      {...provided.dragHandleProps}
			      {...provided.draggableProps}
			      >
			      {itemDescription}
			      <p>{}</p>
			    </div>
		    )
		  }
		</Draggable>
		);
};

export default CategoryItem